import { Injectable } from '@angular/core';
import { UserDto } from '../common/common.entity';
import { Store } from '../service/store.service';
import { forkJoin, Observable, of } from 'rxjs';
import { tap } from 'rxjs/operators';
import { AuthURL } from '../config/auth-url.config';
import { AuthState } from './auth.state';
import { DataStoreService } from '../service/data-store.service';
@Injectable()
export class AuthStore extends Store<AuthState> {
  httpService:any;
  constructor(private _storeservice: DataStoreService) {
    super(new AuthState());
    this._storeservice.currentStore.subscribe((res: any) => {
      if(res){
        this.httpService = res['HTTPSERVICE']
      }
    })

  }
  // constructor(private httpService: HttpService) {
  // }

  addAuthInfo(user: UserDto): void {
    this.setState({ ...this.state, user });
  }

  getAuthInfo(): Observable<any> {
    console.log(this.state);
    if (this.state.user) {
      return of(this.state.user);
    } else {
      return forkJoin([this.httpService.get(AuthURL.EndPoints.auth.user.userInfo)]).pipe(
        tap(([user]) => {
          return user;
        })
      );
    }
  }
}
