import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AuthService } from '../../../@core/service/auth.service';
import { AccessManagementCommonService } from '../../../@core/urls/access-management-common.service';

@Component({
  selector: 'app-organization-dropdown',
  templateUrl: './organization-dropdown.component.html',
  styleUrls: ['./organization-dropdown.component.scss']
})
export class OrganizationDropdownComponent implements OnInit {
  @Input() isShow: boolean;
  @Input() Organization: any;
  @Output() changeOrganization = new EventEmitter();
  orgList: any[];

  constructor(public accessManagementService: AccessManagementCommonService, public auth: AuthService) {
     // This is intentional
  }

  ngOnInit() {
    this.getOrgList();
  }

  /**
   * @description returning selected organization details on event emitter
   * @param orgID organization id
   */
  onSelectionChange(orgID) {
    this.changeOrganization.emit(orgID);
  }

  /**
   * @description fetch organization list from backend
   */
  getOrgList() {
    this.accessManagementService.getOrganizationList().subscribe(res => {
      if (res['data'] && res['data'].length) {
        this.orgList = res['data'].sort((a, b) => a.organizationname?.localeCompare(b.organizationname));
      }
    });
  }

  /**
   * @description check isAdmin or not
   */
  get displayCondn(): boolean {
    return this.auth.isAdmin() && this.isShow;
  }
}
