import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-rbac-pageaccess/@core/permissions/permission.store';
import { RBACINFO } from './pics-rbac-pageaccess/@core/urls/rbac-url.config';
import { DataStoreService } from './pics-rbac-pageaccess/@core/service/data-store.service';

@Component({
  selector: 'rbac-pageaccess',
  template: `
   <lib-pageaccess></lib-pageaccess>
  `,
  styles: [
  ]
})
export class RbacPageaccessComponent implements OnInit {

  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public accessEvent!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: DataStoreService
  ) {
  }
  ngOnInit() {
     this.accessEvent.subscribe(val => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
      this._storeservice.setData('HTTPSERVICE',val.httpService);
    })
  }

}
